
* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  margin: 0;
}
.myHomeContainer {
    width: 100%;
    display: flex;


}



/*typography*/
.logo {
    font-size: 1.5rem;
    margin-left: 1.2em;
    font-weight: bold;
    cursor: pointer;
    color: white;
}
/*layout*/

.navbarContainer {
    background-color: rgb(27, 27, 34);
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;

}

.navbarLink {
    margin-right: .7em;
    font-size: .8rem;
    cursor: pointer;
}

.navbarImg {
    cursor: pointer;
    width: 31.5px;
    height: 31.5px;
    object-fit: cover;
    border-radius: 50%;
}

.navbarLeft {
    flex: 3 1;
    

}
.navbarCenter {
    flex: 5 1;
   
    
}
.searchBar {
    height: 30px;
    width: 100%;
    border-radius: 28px;
    background-color: white;
    display: flex;
    align-items: center;
}

.searchInput {
    width: 80%;
    border: none;

}
.searchInput:focus {
    outline: none;
}

.searchIcon {
    margin-left: .6em;
    font-size: 20px !important;
}

.navbarRight {
    color: white;
    flex: 4 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    
    
    
}
.navbarIcons {
    display: flex;
}
.navbarIconBadge {
    font-size: .7rem;
    position: absolute;
    width: 16px;
    color: white;
    height: 16px;
    background-color: red;
    top: -5px; right: -5px;
    border-radius: 50%;
    display: flex;
    justify-content: center;

}
.navbarIconItem {
    margin-right: .9em;
    cursor: pointer;
    position: relative;
}


/*media query*/
@media(max-width: 1000px) {
    .searchBar {
       display: none;
       
    }
    .timeline {
        display: none;
    }
    .navbarContainer {
        min-width: 600px;
    }


}


.sidebar {
    height: calc(100vh - 50px);
    flex: 3 1;
    overflow-y: scroll; 
    top: 50px;
    position: -webkit-sticky;
    position: sticky;  
}
::-webkit-scrollbar {
    width: 6px;
}
::-webkit-scrollbar-thumb {
    background-color: rgb(18, 18, 20);
}
::-webkit-scrollbar-track {
    background-color: wheat;
}
.sidewrapper {
    padding: 1.2em;
}
.sidebarList {
    list-style: none;
    margin: 0; padding: 0;
}
.sidebarIcon {
    margin-right: .5em;
}
.sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 1.75em;
}
.sidebarHr {
    margin: 1.2em 0;
}
.sidebarButton {
    border: none;
    font-weight: 700;
    width: 130px;
    border-radius: 4px;
    padding: .8em;
}
.sidebarFriendList {
    list-style: none;
    margin: 0;
    padding: 0;
}


.sidebarFriendItem {
    display: flex;
    align-items: center;
    margin-bottom: .9em;
}
.sidebarFriendImg {
    width: 30px;
    height: 30px;
    object-fit: cover;
    margin-right: .7em;
    border-radius: 50%;
    
}
.feed {
    flex: 5.5 1;
}
.feedWrapper {
    padding: 1.2em;
}
.shareContainer {
    height: 170px;
    width: 100%;
    border-radius: 10px;
    box-shadow: 5px 10px 25px -7px rgba(0,0,0,0.7);

    
}
.shareImg {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: .7em;
}
.shareWrapper {
    padding: .7em;
}
.shareFisrt {
    display: flex;
    align-items: center;
}
.shareInput {
    width: 75%;
    border: none;
}
.shareHr {
    margin: 1.2em;
}
.shareInput:focus {
    outline: none;
}
.shareLast {
    display: flex;
    align-items: center;
    justify-content: space-between;
   
}
.shareOptionsContainer {
    display: flex;
    margin-right: 1.2em;
}
.shareOption {
    cursor: pointer;
    margin: .9em;
    display: flex;
    align-items: center;
    
}
.shareIcon {
    font-size: 1rem;
    margin: .4em;
}
.shareOptionText {
    font-size: .8rem;
    font-weight: 600;
}
.shareButton {
    font-weight: 500;
    padding: .3em;
    border-radius: 5px;
    border: none;
    color: white;
    background-color: rgb(15, 18, 173);
    cursor: pointer;
    margin-right: .6em;
}
.postContainer {
    width: 100%;
    margin: 2em 0;
    border-radius: 10px;
    box-shadow: 5px 10px 25px -7px rgba(0,0,0,0.7);
}
.postWrapper {
    padding: .7em;
}
.postFirst {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.firstLeft {
    display: flex;
    align-items: center;
}
.postProfileImg {
    border-radius: 50%;
    height: 32px;
    width: 32px;
    object-fit: cover;
    
}
.postUsername {
    margin: 0 .7em;
    font-size: .9rem;
    font-weight: 500;

}
.postDate {
    font-size: .7rem;
}
.postImg {
    max-height: 500px;
    width: 100%;
    object-fit: contain;
    margin-top: 1.2em;
}
.postCenter {
    margin: 1.2em 0;
}
.postLast {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.postLastLeft {
    display: flex;
    align-items: center;
}
.likeIcon {
    cursor: pointer;
    height: 24px;
    width: 24px;
    margin-right: .3em;
}

.postLikeCounter {
    font-size: .9em;
}
.postCommentText {
    font-size: .85em;
    border-bottom: 1px solid gray;
    cursor: pointer;
}

.rightbar {
    flex: 3.5 1;
}
.rightbarWrapper {
    padding: 1.2em 1.2em 0 0;
}
.birthdayImg {
    width: 60px;
    height: 60px;
    object-fit: cover;
    margin-right: .7em;
}
.birthdayText {
    font-size: .9rem;
    font-weight: 300;
}
.birthdayContainer {
    display: flex;
    align-items: center;
}
.rightbarAdvert {
    border-radius: 12px;
    margin: 1.8em 0;
    width: 100%;
}

.rightbarFriendList {
    list-style: none;
    margin: 0;
    padding: 0;
}
.rightbarFriend{
    display: flex;
    align-items: center;
    margin-bottom: .9em;
    
}
.rightImgContainer {
    position: relative;
    margin-right: .6em;
}
.rightbarImg {
    width: 60px;
    height: 60px;
    object-fit: cover;
    border-radius: 50%;
}
.rightbarUserName {
    font-weight: 500;
}
.rightbarOnline {
    border-radius: 50%;
    width: 12px;
    height: 12px;
    background-color: green;
    position: absolute;
    bottom: 0; right: 10px; 
    border: 2px solid wheat;

}
.rightbarTitle {
    margin-bottom: 1.2em;
}
